<template>
  <h1 class="text-error" v-if="redirectMsg">{{ redirectMsg }}</h1>

  <form @submit="onSubmit">
    <div class="outer">
      <h1 class="caption">Login</h1>

      <div v-if="errors" class="text-error">
        <p class="word-break-all" v-for="error in errors" :key="error">{{ error }}</p>
      </div>

      <div class="input">
        <input v-model="email" name="email" type="email" required />
        <span class="floating-label">Email address</span>
      </div>
      <div class="input">
        <input v-model="password" name="password" type="password" required />
        <span class="floating-label">Password</span>
      </div>

      <div class="">
        <button type="submit" class="btn btn-icon">Login<i class="fas fa-sign-in-alt"></i></button>
      </div>
    </div>
  </form>
</template>

<script>
export default {
  data() {
    return {
      username: '',
      email: '',
      password: '',
      errors: null,
    }
  },

  props: {
    redirectMsg: {},
  },

  methods: {
    onSubmit(e) {
      //console.log(e)
      e.preventDefault()
      //console.log(this.username, this.email, this.password)
      console.log('Login form submitted')

      this.$store
        .dispatch('user/login', { email: this.email, password: this.password })
        .then((data) => {
          if (data.error) return (this.errors = data)
          this.$router.push({ name: 'home', params: { redirectMsg: 'Successfully logged in!' } })
          console.log('Redirecting to home...')
          //console.log(data)
        })
        .catch((err) => console.log(err))
    },
  },
}
</script>

