<template>
  <form @submit="onSubmit">
    <div class="outer">
      <h1 class="caption">Register</h1>

      <div v-if="errors" class="text-error">
        <p v-for="error in errors" :key="error">{{ error.constraints[Object.keys(error.constraints)[0]] }}</p>
      </div>

      <div class="input">
        <input v-model="username" name="username" type="text" required />
        <span class="floating-label">Username</span>
      </div>
      <div class="input">
        <input v-model="email" name="email" type="email" required />
        <span class="floating-label">Email address</span>
      </div>
      <div class="input">
        <input v-model="password" name="password" :type="type" required />
        <span class="floating-label">Password</span>
        <i v-if="passwordVisible" @click="showPassword" :class="passwordIcon" class="password-hide"></i>
      </div>
      <div class="input">
        <input :type="type" required />
        <span class="floating-label">Password again</span>
      </div>

      <div class="">
        <button type="submit" class="btn btn-icon">Register<i class="fas fa-user-plus"></i></button>
      </div>
    </div>
  </form>
</template>

<script>
export default {
  data() {
    return {
      username: '',
      email: '',
      password: '',
      type: 'password',
      passwordIcon: 'fas fa-eye',
      passwordVisible: false,
      errors: null,
    }
  },

  methods: {
    onSubmit(e) {
      //console.log(e)
      e.preventDefault()
      //console.log(this.username, this.email, this.password)
      console.log('Form submitted')

      fetch(process.env.VUE_APP_API_BASE_URL + '/auth/register', {
        method: 'post',
        credentials: 'include',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          username: this.username,
          email: this.email,
          password: this.password,
        }),
      })
        .then((res) => res.json())
        .then((data) => {
          if (!data.success) return (this.errors = data)
          this.$router.push({ name: 'home', params: { redirectMsg: 'Account successfully created!' } })
        })
    },
    showPassword() {
      if (this.type === 'password') {
        this.type = 'text'
        this.passwordTooltip = 'Hide password'
        this.passwordIcon = 'fas fa-eye-slash'
      } else {
        this.type = 'password'
        this.passwordTooltip = 'Show password'
        this.passwordIcon = 'fas fa-eye'
      }
    },
  },
  watch: {
    password(newValue) {
      if (newValue.length != 0) {
        this.passwordVisible = true
      } else {
        this.passwordVisible = false
      }
    },
  },
}
</script>

